import React, { useState } from 'react';
import { useFormik } from 'formik';
import axios from 'axios';
import * as Yup from 'yup';

function PowerModulo() {

    const [result, setResult] = useState(" ");
    
    const formik = useFormik({

        initialValues: {
            a: '',
            b: '',
            m: '',
        },
        validationSchema: Yup.object().shape({
            a: Yup.number()
            .required("Wprowadź wartość a")
            .positive("Wprowadź dodatnią wartość a")
            .integer("Wprowadź wartość całkowitą a"),
            b: Yup.number()
            .required("Wprowadź wartość b")
            .positive("Wprowadź dodatnią wartość b")
            .integer("Wprowadź wartość całkowitą b"),
            m: Yup.number()
            .required("Wprowadź wartość m")
            .positive("Wprowadź wartość dodatnią m")
            .integer("Wprowadź wartość całkowitą m"),
          }),
        onSubmit: async values => {
            
        
                try {
            const response = await axios.post('http://localhost:8081/api/numbertheory/powermodulo', values);
            if (response.status !== 200) {
                throw new Error('Failed to fetch data');
            }
            
            else {
            setResult(response.data);
            }
    
        } catch (error) {
            setError(error.message);
        }
    
            
        },


    })


    return (
    <div className="Power Modulo"  class="onepiece">
   <h3>Potęgowanie modulo</h3>
    <form onSubmit={formik.handleSubmit}>
    <input type="number" id="a" name="a" value={formik.values.a} onChange={formik.handleChange} onBlur={formik.handleBlur} />
    <sup>
    <input type="number" id="b" name="b" value={formik.values.b} onChange={formik.handleChange} onBlur={formik.handleBlur} />
    </sup>
    = {result} (mod
    <input type="number" id="m" name="m" value={formik.values.m} onChange={formik.handleChange} onBlur={formik.handleBlur} />)
    <input type="submit" /><button type="button" onClick={formik.handleReset}>Resetuj</button>
    {formik.touched.a && formik.errors.a ? (
            <div className="error">{formik.errors.a}</div>
            ) : null}
            {formik.touched.b && formik.errors.b ? (
            <div className="error">{formik.errors.b}</div>
            ) : null}
            {formik.touched.m && formik.errors.m ? (
            <div className="error">{formik.errors.m}</div>
            ) : null}
    </form>

</div>
);

}

export default PowerModulo;